import formidable from "formidable";
import cloudinary from "cloudinary";
import { NextApiRequest, NextApiResponse } from "next";
import dbConnect from "../../lib/db";
import { Video } from "../../models/Video"; // Import the Video model
import { Image } from "../../models/Image"; // Import the Image model
import { Movie } from "../../models/Movie"; // Import the Movie model

// Configure Cloudinary
cloudinary.v2.config({
  cloud_name: process.env.CLOUDINARY_CLOUD_NAME,
  api_key: process.env.CLOUDINARY_API_KEY,
  api_secret: process.env.CLOUDINARY_API_SECRET,
});

// Disable Next.js default body parsing
export const config = {
  api: {
    bodyParser: false, // Disable the default body parser
  },
};

const uploadVideo = async (req: NextApiRequest, res: NextApiResponse) => {
  // Connect to the database
  await dbConnect();

  const form = new formidable.IncomingForm();

  form.parse(req, async (err, fields, files) => {
    if (err) {
      console.error("Error parsing form:", err);
      return res.status(500).json({ error: "Error processing form" });
    }

    const videoFile = files.video?.[0];
    const imageFile = files.image?.[0]; // Assuming there's an image upload

    if (!videoFile) {
      return res.status(400).json({ error: "No video file uploaded" });
    }

    try {
      // Upload video to Cloudinary
      const videoResult = await cloudinary.v2.uploader.upload(
        videoFile.filepath,
        {
          resource_type: "video",
        }
      );

      // Optionally, upload image to Cloudinary (if available)
      // let imageUrl = null;
      // if (imageFile) {
      //   const imageResult = await cloudinary.v2.uploader.upload(
      //     imageFile.filepath,
      //     {
      //       resource_type: "image",
      //     }
      //   );
      //   imageUrl = imageResult.secure_url;
      // }

      // Create the movie document (if not already created)
      const movieId = fields.movieId;
      let movie = await Movie.findById(movieId);
      if (!movie) {
        return res.status(400).json({ error: "Movie not found" });
      }

      // Create the Video document in the database
      const video = new Video({
        movieId,
        videoUrl: videoResult.secure_url,
      });
      await video.save();

      // Create the Image document if an image was uploaded
      // if (imageUrl) {
      //   const image = new Image({
      //     movieId,
      //     imageUrl,
      //   });
      //   await image.save();
      // }

      // Return the response with video and image URLs
      res.status(200).json({
        videoUrl: videoResult.secure_url,
        // imageUrl: imageUrl || null, // Null if no image uploaded
      });
    } catch (uploadError) {
      console.error("Error uploading video to Cloudinary:", uploadError);
      res.status(500).json({ error: "Error uploading video" });
    }
  });
};

export default uploadVideo;
